// vendors
const getVue = () => import(/* webpackChunkName: "vue" */ 'vue');
// modules
const getModal = () => import(/* webpackChunkName: "modal" */ '%modules%/modal/modal');
const getForm = () => import(/* webpackChunkName: "form" */ '%modules%/form/form');
const getSlider = () => import(/* webpackChunkName: "slider" */ '%modules%/slider/slider');
const getDropdown = () => import(/* webpackChunkName: "dropdown" */ '%modules%/dropdown/dropdown');
const getTooltip = () => import(/* webpackChunkName: "tooltip" */ '%modules%/tooltip/tooltip');
const getAccordion = () => import(/* webpackChunkName: "accordion" */ '%modules%/accordion/accordion');
const getAnchorMenu = () => import(/* webpackChunkName: "anchor-menu" */ '%modules%/anchor-menu/anchor-menu');
const getAnimationCubes = () => import(/* webpackChunkName: "animation-cubes" */ '%modules%/animation-cubes/animation-cubes');
const getAnimationPatternLines = () => import(/* webpackChunkName: "animation-pattern-lines" */ '%modules%/animation-pattern-lines/animation-pattern-lines');
const getInSight = () => import(/* webpackChunkName: "in-sight" */ '%modules%/in-sight/in-sight');
const getSearchFormPage = () => import(/* webpackChunkName: "search-form-page" */ '%modules%/search-form-page/search-form-page');
const getEvent = () => import(/* webpackChunkName: "contacts" */ '%modules%/event/event');
const getTable = () => import(/* webpackChunkName: "table" */ '%modules%/table/table');

// vue components
import Header from '%modules%/header/header.vue';
import Catalog from '%modules%/catalog/catalog.vue';
import AddToCart from '%modules%/add-to-cart/add-to-cart.vue';
import Cart from '%modules%/cart/cart.vue';
// helpers
const getFloatButton = () => import(/* webpackChunkName: "float-button" */ '~/js/helpers/float-button');
const getUnderBlock = () => import(/* webpackChunkName: "under-block" */ '~/js/helpers/under-block');
//globals

// Main IIFE
(function () {
	document.addEventListener('DOMContentLoaded', e => {
		initForms();
		initSelects();
		initHeader();
		initFooterMaterialsButton();
		initCatalog();
		initModals();
		initTooltips();
		initAccordions();
		initSliders();
		initInSight();
		initAnchorMenu();
		initFloatButtons();
		initUnderBlock();
		initAnimationCubes();
		initAnimationPatternLines();
		initAddToCart();
		initSearchFormPage();
		initCart();
		initEvent();
		initTable();
	});

	window.initForms = function () {
		let selector = 'data-form',
			forms = [...document.querySelectorAll(`[${selector}]`)];

		if (forms.length) {
			getForm().then(({FormFactory})=>{
				new FormFactory({ selector: selector });
			})

		}
	}

	window.initSelects = function () {
		let selector = 'data-dropdown',
			elements = [...document.querySelectorAll(`[${selector}]`)];

		if (elements.length) {
			getDropdown().then(({DropdownFactory})=>{
				new DropdownFactory({selector});
			})

		}
	}

    window.scrollAnchors = (hash) => {
		getAnchorMenu().then(({AnchorMenu})=>{
			AnchorMenu.prototype.outsideScrollAnchors(hash);
		})
    };

	window.initModals = function() {
		let selector = 'data-modal',
			btns = [...document.querySelectorAll(`[${selector}]`)];

		if (btns.length) {
			getModal().then(({ModalFactory})=>{
				new ModalFactory({ selector: selector });
			})
		}
	}

	function initHeader() {
		if (document.querySelector('#header')) {
			getVue().then((Vue)=>{
				window.headerApp = new Vue.default({
					el: "#header",
					components: {
						'header-component': Header
					}
				})
			})

		}
	}
	function initFooterMaterialsButton() {
		let footerMaterials = document.querySelector('[data-footer-materials]')
		if (footerMaterials) {
			footerMaterials.addEventListener('click',(e)=>{
				e.preventDefault()
				window.headerApp.$children[0].toggleDesktopMenu()
				window.headerApp.$children[0].toggleDesktopMenuSecondLevel(true)

			})
		}
	}
	function initCatalog() {
        let selector = 'data-catalog',
        elements = document.querySelectorAll(`[${selector}]`);
		if (elements.length) {
			elements.forEach(element=>{
				getVue().then((Vue)=>{
					window.catalogApp = new Vue.default({
						el: element,
						components: {
							'catalog': Catalog
						}
					})
				})
			})
		}
    }

	function initTooltips() {
		let selector = 'data-tooltip',
			btns = [...document.querySelectorAll(`[${selector}]`)];

		if (btns.length) {
			getTooltip().then(({TooltipFactory})=>{
				new TooltipFactory({ selector: selector });
			})
		}
	}

	function initAccordions() {
		let selector = 'data-accordion',
			accordions = [...document.querySelectorAll(`[${selector}]`)];

		if (accordions.length) {
			getAccordion().then(({AccordionFactory})=>{
				new AccordionFactory({ selector: selector });
			})
		}
	}

	function initSliders() {
		let selector = 'data-slider',
			sliders = [...document.querySelectorAll(`[${selector}]`)];

		if (sliders.length) {
			getSlider().then(({SliderFactory})=>{
				new SliderFactory({ selector: selector });
			})

		}
	}

	function initInSight() {
		let selector = 'data-in-sight',
			elements = [...document.querySelectorAll(`[${selector}]`)];

		if (elements.length) {
			getInSight().then(({InSightFactory})=>{
				new InSightFactory({
					selector: selector,
					func: (el)=>{
						el.querySelectorAll('.is-init-animation').forEach(anim => anim.classList.remove('is-init-animation'))
					} });
			})
		}
	}

	function initAnchorMenu() {
        let selector = 'data-anchor-menu',
        elements = document.querySelectorAll(`[${selector}]`);
        if (elements.length) {
			getAnchorMenu().then(({AnchorMenuFactory})=>{
				new AnchorMenuFactory(selector);
			})
        }
    }

	function initFloatButtons(){
		let selector = 'data-float-button',
        elements = document.querySelectorAll(`[${selector}]`);
        if (elements.length) {
			elements.forEach(el=>{
				getFloatButton().then((FloatButton)=>{
					new FloatButton.default(el);
				})
			})
        }
	}
	function initUnderBlock(){
		let selector = 'data-under-block',
        elements = document.querySelectorAll(`[${selector}]`);
        if (elements.length) {
			elements.forEach(el=>{
				getUnderBlock().then((UnderBlock)=>{
					new UnderBlock.default(el, selector);
				})
			})
        }
	}
	function initAnimationCubes(){
		let selector = 'data-animation-cubes',
        element = document.querySelector(`[${selector}]`);
        if (element) {
			getAnimationCubes().then((AnimationCubes)=>{
				new AnimationCubes.default(element)
			})

			let timeout;

			const io = new IntersectionObserver(entries => {
				entries.forEach(entry => {
					if (entry.intersectionRatio >= .8) {
						timeout = setTimeout(() => {
							document.dispatchEvent(new Event('cubes-in-viewport'));
						}, 800);
					} else {
						clearTimeout(timeout);
					}
				})
			}, {threshold: [.8]});

			io.observe(element);

        }
	}
	function initAnimationPatternLines(){
		let selector = 'data-animation-pattern-lines',
        elements = [...document.querySelectorAll(`[${selector}]`)];
        if (elements.length) {
			elements.forEach(element => {
				getAnimationPatternLines().then((AnimationPatternLines)=>{
					new AnimationPatternLines.default(selector, element)
				})
			})
        }
	}
	function initAddToCart(){
		let selector = 'data-add-to-cart',
        elements = document.querySelectorAll(`[${selector}]`);
		if (elements.length) {
			elements.forEach(element=>{
				getVue().then((Vue)=>{
					new Vue.default({
						el: element,
						components: {
							'add-to-cart': AddToCart
						}
					})
				})

			})

		}
	}

	function initSearchFormPage(){
		let selector = 'data-search-form-page',
        element = document.querySelector(`[${selector}]`);
        if (element) {
			getSearchFormPage().then((SearchFormPage)=>{
				new SearchFormPage.default(element, selector)
			})
        }
	}

	function initCart(){
		let selector = 'data-cart-app',
        element = document.querySelector(`[${selector}]`);
		if (element) {
			getVue().then((Vue)=>{
				new Vue.default({
					el: element,
					components: {
						'cart-app': Cart
					}
				})
			})
		}
	}

	function initEvent(){
		getEvent().then((Event) => {
			new Event.default();
		});
	}

	function initTable(){
		let selector = 'data-table',
			element = document.querySelector(`[${selector}]`);
		getTable().then((Table) => {
			new Table.default(element, selector);
		});
	}
})();
