<template lang="pug">
include ../button/button
include ../link/link

.search-form(
    :class="{'is-active':focusedSearch || searchValue}")
    .search-form__wrapper
        +link({
            vue: true,
            prefix: 'templatePath',
            noLink: true,
            icon:"search",
            iconClass: 'icon--size--large'
            })(
                @click.prevent="searchResponse ?redirectSearchPage():(searchValue?searchRequestDebounced():focusInput())"
                ).link--color--dark-55.search-form__submit
            
        .search-form__input

            input(
                ref="input"
                v-model.trim="searchValue" 
                @focus="focusedSearch = true" 
                @blur="focusedSearch = false"
                autocomplete="off"
                autocapitalize="off"
                autocorrect="off"
                spellcheck="false"
                type="text"
                inputmode="search"
                :placeholder="searchData.placeholder"
                @input="searchRequestDebounced()"
                @keydown="keydownEnter($event)"
                )
        
        +link({
            vue: true,
            prefix: 'templatePath',
            noLink: true,
            icon:"cross-in-circle"
            })(
                v-if="searchValue"
                @click.prevent="clear()").link--color--dark-55.search-form__clear

    +link({
        vue: true,
        prefix: 'templatePath',
        noLink: true,
        icon:"cross",
        iconClass: 'icon--size--large'
        })(
            @click.prevent="toggleSearch()"
    ).link--color--dark-55.search-form__close
    
    search-results(
        v-if="searchValue && searchResponse && searchResponse.groups.length"
        :searchResponse="searchResponse")

</template>

<script>
import Axios from 'axios';
import SearchResults from '../search-results/search-results.vue'
import debounce from 'lodash/debounce';

export default {
    name: "search-form",
    props:{
        searchData: Object,
        toggleSearch: Function,
    },
    data() {
        return{
            focusedSearch: false,
            searchValue: "",
            searchResponse: null,
            cooldownRequest: false,
            isLoading: false,
            templatePath: window.SITE_TEMPLATE_PATH_JS
        }
    },
    components: {
        "search-results": SearchResults
    },
    methods: {
        searchRequest(){
            this.isLoading = true
			Axios({
                method: 'POST',
                url: this.searchData.actionURL,
                params: {
                    "searchString": this.searchValue
                }
            }).then(response => {
                this.searchResponse = response.data
                this.isLoading = false
            }).catch(error => {
                console.error(error)
            })
        },
        clear(){
            this.searchValue = '',
            this.searchResponse = null
        },
        redirectSearchPage(){
            location.href = this.searchResponse.link
        },
        keydownEnter(event){
            if(event.keyCode === 13 && this.searchResponse){
                this.redirectSearchPage()
            }
        },
        focusInput(){
            this.$refs.input.focus()
        }
    },
    mounted(){
    },
    created(){
        this.searchRequestDebounced = debounce(this.searchRequest, 300)
    }
}
</script>