<script>
import Axios from 'axios';
export default {
    props:{
        url: String,
        id: String
    },
    data(){
        return {
            
        }
    },
    computed:{
        active(){
            if(window.cart && window.cart.items && window.cart.items.length) {
                if(window.cart.items.includes(this.id) || window.cart.items.includes(+this.id)){
                    return true
                } else {
                    return false
                }
            } 
        }
    },
    methods: {
        clickOnButton(event){
            if(!this.active){
                event.preventDefault()
                this.request()
            }
        },
        request(){
            Axios({
                method: 'POST',
                url: this.url,
                params: {
                    "ID": this.id
                }
            }).then(res => {
                if(res.data.success){
                    if(!window.cart && !window.cart.items){
                        window.cart = {}
                        window.cart.items = []
                    }
                    window.cart.items.push(this.id)
                    window.cart.count = res.data.count

                }
            }).catch(error => {
                console.log(error)
            })
        }
    }
}
</script>