<script>
export default {
    name: "count-control",
    props: {
        product: Object,
        update: Function
    },
    data(){
        return {
            message: ''
        }
    },
    methods: {
        inputQuantity(event){
            if (!event.isTrusted) return false
            this.product.quantity = this.count(+event.target.value);
           
            this.update();
        },
        additionQuantity(){
            this.product.quantity = this.count(+this.product.quantity + this.product.step);

            this.update();
        },
        decreaseQuantity(){
            this.product.quantity = this.count(+this.product.quantity - this.product.step);

            this.update();
        },
        count(value){
            let currentValue = +value || this.product.min,
                step = +this.product.step,
                max = +this.product.max,
                maxRemainder = max % step,
                stepRemainder = currentValue % step;

            if (stepRemainder){
                currentValue = currentValue + (step - stepRemainder);
                this.message = `Кратно ${step}`
                setTimeout(()=>{
                    this.message = ''
                },2000)
            }

            if (currentValue >= max) {
                currentValue = max - maxRemainder;
                this.message = `Доступно ${max}`
                setTimeout(()=>{
                    this.message = ''
                },2000)
            }

            return currentValue;
        }
    }
}
</script>