<template lang="pug">
    
</template>
<script>
import store from './store'
import Filters from '../filters/filters.vue';
import Chips from '../chips/chips.vue';
import Dropdown from '../dropdown/dropdown';
import MobileFilters from '../mobile-filters/mobile-filters.vue';
import { mapGetters, mapMutations } from "vuex";
import updateURLParameter from "../../../js/helpers/update-url";
const getInSight = () => import(/* webpackChunkName: "in-sight" */ '%modules%/in-sight/in-sight');
const getModal = () => import(/* webpackChunkName: "modal" */ '%modules%/modal/modal');
const getAccordion = () => import(/* webpackChunkName: "accordion" */ '%modules%/accordion/accordion');

export default {
    store: store,
    components:{
        "chips": Chips,
        "filters": Filters,
        "mobile-filters": MobileFilters
    },
    data(){
        return {
            typeCatalog: "list",
            htmlElements: {},
            autoLoadCount: 0,
            sortDropdownRefs: ['sortDropdown', 'sortDropdownMobile']
        }
    },
    computed: {
        ...mapGetters(['filterSelectedItems','filterCountFormated'])
    },
    methods:{
        pagination(event, more = false){
            event.preventDefault()
            let url = event.currentTarget.href
            this.$store.dispatch('updateContent',{
                htmlElements: this.htmlElements,
                url,
                more,
                callbackPaginationUpdate: this.initPaginationButtons,
                callbackPaginationNoMore: this.scrollToTopCatalog,
                callbackPaginationMore: this.checkAutoLoadCount,
                callbackContentUpdate: this.callbackContentUpdate
            });
            
        },
        sort(select) {
            let type = select.value;
            let url = updateURLParameter(window.location.href, "sort", type);

            this.$store.dispatch('updateContent',{
                htmlElements: this.htmlElements,
                url,
                more: false,
                callbackPaginationUpdate: this.initPaginationButtons,
                callbackPaginationNoMore: this.scrollToTopCatalog,
                callbackPaginationMore: this.checkAutoLoadCount,
                callbackContentUpdate: this.callbackContentUpdate
            });
        },
        initPaginationButtons(){
            let selector = 'data-pagination-button',
            buttons = this.$refs.pagination.querySelectorAll(`[${selector}]`);
            if (buttons.length){
                buttons.forEach(button => {
                    let more = button.getAttribute(selector)

                    button.addEventListener('click',event=>{
                        this.pagination(event, more)
                    })

                    if(more) {
                        this.initAutoLoadCount(button)
                    }

                });
            }
           
        },
        scrollToTopCatalog(){
            let scrollTop = this.$refs.catalog.getBoundingClientRect().top + window.pageYOffset,
            offTop = document.querySelector('#header').offsetHeight
            window.scrollTo({
                top: scrollTop - offTop,
                behavior: 'smooth'
            });
        },
        initInSight(){
            let selector = 'data-in-sight',
                elements = [...this.$el.querySelectorAll(`[${selector}]`)];

            if (elements.length) {
                getInSight().then(({InSightFactory})=>{
                    new InSightFactory({
                        selector: selector,
                        func: (el)=>{
                            el.querySelectorAll('.is-init-animation').forEach(anim => anim.classList.remove('is-init-animation'))
                        } });
                })
            }
        },
        initAccordions() {
            let selector = 'data-accordion',
                accordions = [...this.$el.querySelectorAll(`[${selector}]`)];

            if (accordions.length) {
                getAccordion().then(({AccordionFactory})=>{
                    new AccordionFactory({ selector: selector });
                })
            }
        },
        initModals() {
            let selector = 'data-modal',
                btns = [...this.$el.querySelectorAll(`[${selector}]`)];

            if (btns.length) {
                getModal().then(({ModalFactory})=>{
                    new ModalFactory({ selector: selector });
                })
            }
        },
        initSortDropdown() {
            let selector = 'data-dropdown';
            this.sortDropdownRefs.forEach(ref => {
			    new Dropdown(this.$refs[ref], selector, {
			    	changeValueCallback: this.sort
			    });
            });
        },
        resetSortDropdown() {
            console.log("reset sort 234")
            this.sortDropdownRefs.forEach(ref => {
                let select = this.$refs[ref].querySelector("select");
                if (select) {
                    let defaultOption = select.querySelector("option[selected]") || select.querySelector("option");
                    select.value = defaultOption.value;
                    if (select.sumo) {
                        select.sumo.reload();
                    }
                }
            });
        },
        checkAutoLoadCount(){
            if(this.autoLoadCount > 0) {
                this.autoLoadCount--
            } else {
                this.autoLoadCount = 4
            }
        },
        initAutoLoadCount(button){
            if(this.autoLoadCount > 0) {
                const observer = new IntersectionObserver( 
                ([e]) => {
                    if (e.intersectionRatio > 0) {
                        button.dispatchEvent(new Event('click'));
                    }

                },{
                    threshold: [0]
                });

                observer.observe(button)
            }
        },
        callbackContentUpdate(){
            this.initModals()
            this.initAccordions()
            this.initInSight()
        },
    },
    mounted(){
        console.log("catalog mounted")
        document.addEventListener("filters:updated", this.resetSortDropdown);

        if (this.$refs.sortDropdown)
            this.initSortDropdown();

        if (this.$refs.pagination)
            this.initPaginationButtons();

        this.htmlElements = {	
            filters:this.$refs.filters,
            catalog:this.$refs.catalog,
            pagination: this.$refs.pagination,
            count: this.$refs.count
		}
    }
}
</script>