import $ from 'jquery';
import PerfectScrollbar from 'perfect-scrollbar';

export default class Table {
	constructor(el, selector, params) {
		if (!el) return false;

		this.root = el;
		this.selector = selector;

		const defaultParams = {
			auth: window.isAuth
		};

		this.params = { ...defaultParams, ...params };

		this._init();
	}

	_init() {
		this._matchHTML();
		this._binds();
		this._initEventListeners();
	}

	_matchHTML() {
		this.cells = $(' > table > tbody > tr > td, > table > thead > tr > th:not(:first-child)', $(this.root)).toArray();
	}

	_binds() {
		['_cellHighlight', '_containerHandler'].forEach(f => this[f] = this[f].bind(this));
	}

	_initEventListeners() {
		this.cells.forEach(cell => {
			cell.addEventListener('mouseenter', this._cellHighlight);
			cell.addEventListener('mouseleave', (event) => {
				this.cells.forEach(cell => cell.style.backgroundColor = '')
			});
		});

		// this.ps = new PerfectScrollbar(this.root,{
        //     wheelPropagation: false
        // })

		this.root.addEventListener('scroll', this._containerHandler);
	}

	_cellHighlight(e) {
		e.preventDefault();
		let cellIndex = $(e.currentTarget).index();

		this.cells.forEach(cell => {
			let index = $(cell).index();
			if (cellIndex === index) cell.style.backgroundColor = '#F4F9FA';
		});

		e.currentTarget.style.backgroundColor = '#E9F3F5';
	}

	_containerHandler(e) {
		if (this.root.scrollLeft > 0) {
			this.root.classList.add('active-x');
		} else {
			this.root.classList.remove('active-x');
		}

		if (this.root.scrollTop > 0) {
			this.root.classList.add('active-y');
		} else {
			this.root.classList.remove('active-y');
		}
	}
	
}
