<script>
import { Form } from '%modules%/form/form';
import CountControl from '%modules%/count-control/count-control.vue';
import declOfNum from '~/js/helpers/decl-of-num';
import Axios from 'axios';
import debounce from 'lodash/debounce';

export default {
	components: {
		"count-control": CountControl
	},
    data(){
        return {
            type: "commercial",
            cart: window.cart,
            cartAppData: window.cartAppData
        }
    },
	methods: {
		initForm(){
			let form = this.$refs.form;
			if (form) {
				new Form(form);
			}
		},
		updateProducts(item, action) {
            let data = {
                "action": action,
                "id": item.id
                };

            if (action === "update") {
                data.quantity = +item.quantity
            }

			Axios({
                method: 'POST',
                url: this.cartAppData.actionURL,
                data: data
            }).then(response => {
				if(response.data.success){
                	this.cartAppData.items = response.data.items
					this.cart.count = response.data.count
					this.$forceUpdate()
				}

            }).catch(error => {
                console.error(error)
            })
           
        },
	},
    computed:{
        countFormated(){
			return this.cart.count + declOfNum(this.cart.count, [' позиция', ' позиции', ' позиций']);
		}
    },
	created(){
		this.updateProductsDebounce = debounce(this.updateProducts, 300)
	},
	mounted(){
		this.$nextTick(()=>{
			this.initForm();
		})
	},
	updated(){
		this.$nextTick(()=>{
			this.initForm();
		})
	}
}
</script>