<template lang="pug">
    
</template>
<script>
import { DropdownFactory } from '../dropdown/dropdown';
import { mapGetters, mapMutations } from "vuex";
import $ from 'jquery';
import 'sumoselect';
export default {
	props: {
		htmlElements: Object,
		callbackPaginationUpdate: Function,
		callbackPaginationNoMore: Function,
		callbackPaginationMore: Function,
		callbackContentUpdate: Function
	},
    data() {
		return {
			timeout: null,
			seeAllFilters: false
		}
	},
	computed: {
		...mapGetters(['filterItems', 'filterSelectedItems', 'activeItem', 'isFilterItemSelected'])
	},
	methods: {
		buildAndSend(){
			console.log("build and send")
			this.$store.commit('buildRequestData');

			clearTimeout(this.timeout);

			this.timeout = setTimeout(() => {
				this.$store.dispatch('sendFilterData',{
					htmlElements: this.htmlElements,
					callbackPaginationUpdate: this.callbackPaginationUpdate,
					callbackPaginationNoMore: this.callbackPaginationNoMore,
					callbackPaginationMore: this.callbackPaginationMore,
					callbackContentUpdate: this.callbackContentUpdate,
					callbackFinish: () => {
						document.dispatchEvent(new CustomEvent('filters:updated'));
					}
				});
			}, 500);
		},
		setValueChecked(e, itemID, valueID) {
			this.$store.commit('setValueChecked', {
				checked: e.currentTarget.checked,
				itemID,
				valueID
			});

			this.buildAndSend();

		},
		setValueRadio(e, itemID, valueID) {
			this.$store.commit('setValueRadio', {
				checked: e.currentTarget.checked,
				itemID,
				valueID
			});

			this.buildAndSend();

		},
		setValueSelect(select) {
			select.querySelectorAll('option').forEach(opt=>{
				this.$store.commit('setValueChecked', {
					checked: opt.selected,
					itemID: select.dataset.selectId,
					valueID: opt.value
				});
			})

			this.buildAndSend();

		},
		initDropdowns(){
			let selector = 'data-filters-dropdown',
				elements = this.$el.querySelectorAll(`[${selector}]`);

			if (elements.length) {
				
				new DropdownFactory({selector, params:{
					changeCallback: this.setValueSelect
				}});
				
			}
		},
		resetFilterOption(e, item) {
			this.$store.commit('resetFilterOption', item);
			this.buildAndSend();
		},
		reloadDropdowns(){
			let selector = 'data-filters-dropdown',
				elements = this.$el.querySelectorAll(`[${selector}] select`);
			if (elements.length) {
				
				elements.forEach((el)=>{
					if(el.closest(`[${selector}]`).classList.contains('is-inited')){
						$(el)[0].sumo.hideOpts()
						$(el)[0].sumo.reload();
						el.dispatchEvent(new Event('change'));
					}
				})
			}
		},        
		setActiveGroup(item, active){
			if (item) {
				this.$store.commit('setAllActiveItems', {
					checked: false
				});		

				this.$store.commit('setActiveItem', {
					active,
					itemID: item.ID
				});

				this.$store.commit('setAllActiveItems', {
					checked: true
				});		

			} else {
				this.$store.commit('setAllActiveItems', {
					checked: false
				});					
				
				this.$store.commit('setActiveItem', {
					active: false,
					itemID: this.activeItem.ID
				});
			}

			this.buildAndSend();

		},
		setAllActiveItems(checked){
			this.$store.commit('setAllActiveItems', {
				checked
			});

			this.buildAndSend();
		}

	},
	created() {
		this.$store.commit('buildRequestData');
	},
	mounted(){
		this.$nextTick(this.initDropdowns())
	},
	updated(){
		this.$nextTick(this.initDropdowns())
		this.$nextTick(this.reloadDropdowns())
	}
}
</script>