import PerfectScrollbar from 'perfect-scrollbar';

export default class CustomTable {
	constructor(el,selector) {
		this.root = el;

		if (!this.root) return false;
        this.parent = this.root.parentNode
		this._init();
	}

	_init() {
        this.initPerfectScrollbar()
        this.setInModalHeight();
        this.watchScroll()
	}
    initPerfectScrollbar(){
        this.ps = new PerfectScrollbar(this.root,{
            wheelPropagation: false
        })
    }
    setInModalHeight(){
        if(this.root.closest('.modal')){
            this.root.style.height = window.innerHeight - this.root.getBoundingClientRect().top + 'px'
        }
    }
    watchScroll(){
        this.root.addEventListener('ps-scroll-y', () => {
            if(this.ps.reach.y !== 'start') {
                this.parent.classList.add('is-scroll-y')
            } else {
                this.parent.classList.remove('is-scroll-y')
            }
        });
        this.root.addEventListener('ps-scroll-x', () => {
            if(this.ps.reach.x !== 'start') {
                this.parent.classList.add('is-scroll-x')
            } else {
                this.parent.classList.remove('is-scroll-x')
            }
        });
    }
}
