<template lang="pug">
include ../../icon/icon
include ../../button/button

.mobile-menu__item
    .mobile-menu__lang(v-if="item.lang")
        component(
            :is="item.lang == 'rus'?'div':'a'"
            :href="item.link" 
            :class="{'link--color--dark-55':item.lang != 'rus'}"
            ).link.link--color--dark-main.t-base.t-base--size--small.t-base--weight--bold
            span.link__text RUS
        component(
            :is="item.lang == 'eng'?'div':'a'"
            :href="item.link" 
            :class="{'link--color--dark-55':item.lang != 'eng'}"
            ).link.link--color--dark-main.t-base.t-base--size--small.t-base--weight--bold
            span.link__text ENG
    modal(v-else-if="item.modalButton")
        component(
            slot="button"
            :is="tagType"
            :href="item.link"
        ).button.button--size--large.button--type--bordered.button--theme--accent.button--type--bordered-gray.t-base.t-base--size--medium
            .button__wrapper
                span.button__text {{item.text}}

    component(
        v-else
        :is="tagType"
        :href="item.link" 
        @click.stop="onMenuItemClick($event)" 
        :class="{'mobile-menu__link--parent':hasChildren, 't-base--weight--bold':item.bold, 'link--color--dark-55':item.gray}"
        ).link.link--color--dark-main.mobile-menu__link.t-base.t-base--size--large
        
        .link__icon.link__icon--left.link__icon--img(
            v-if="item.beforeTextImg" 
        )
            img(:src="item.beforeTextImg")
        svg(
            v-else-if="item.beforeTextIcon" 
            :class="'icon--'+ item.beforeTextIcon"
            ).icon.link__icon.link__icon--left
            use(:xlink:href="templatePath+'img/sprite.svg#'+ item.beforeTextIcon +'-usage'")
    
        span.link__text {{item.text}}

    
        svg(
            v-if="item.afterTextIcon" 
            :class="'icon--'+ item.afterTextIcon"
            ).icon.link__icon
            use(:xlink:href="templatePath+'img/sprite.svg#'+ item.afterTextIcon +'-usage'")

        .link__counter(v-if="totalCount") {{ totalCount }}


    transition(:name="transition")
        .mobile-menu__children(v-if="showChildren" :class="{'is-opened': isOpened}")
            .mobile-menu__container
                .mobile-menu__group(
                    v-if="groups && groups.length"
                    v-for="group in groups"
                    )
                    mobile-menu-item(
                        v-for="(child, index) in children"
                        v-if="child.group == group.name"
                        :key="index"
                        :parentData="item"
                        :item="child"
                        :data="data"
                        :opened-node="openedNode"
                    )
                mobile-menu-item(
                    v-for="(child, index) in children"
                    v-if="!child.group"
                    :key="index"
                    :parentData="item"
                    :item="child"
                    :data="data"
                    :opened-node="openedNode"
                )
</template>
<script>
import Vue from 'vue';
import { mapGetters, mapMutations } from 'vuex';
import Modal from '../../modal/modal.vue'

export default {
    name: "mobile-menu-item",
    props: {
        parentData: {
            type: Object,
            default: null
        },
        item: {
            type: Object,
            required: true
        },
        data: {
            type: Array
        },
        openedNode: {
            type: Object
        }
    },
	data() {
		return {
            ajaxContent: null,
            cart: window.cart,
            templatePath: window.SITE_TEMPLATE_PATH_JS
		}
    },
    components: {
        'modal': Modal
    },
	computed: {
        ...mapGetters(["openedNodeId"]),
        hasChildren() {
            return (this.item.children && this.item.children.length) ? true : false;
        },
        isOpened() {
            return this.item.opened;
        },
        transition() {
            return 'slide-right';
        },
        showChildren() {
            return this.hasChildren && this.isOpened;
        },
        children() {
            let children = [];
            this.item.children.forEach(childId => {
                const child = this.data.find(item => item.id === childId);
                children.push(child);
            });
            return children;
        },
        tagType() {
            return (this.item.link) ? "a" : "div";
        },
        totalCount() {
            if (this.item.props && this.item.props.cart) {
                return this.cart.count
            } else {
                return false
            }
        },
        groups(){
            let groups = []

            this.children.forEach(node => {
                if (node.group && !groups.filter(e=> e.name === node.group).length) {
                    groups.push({
                        name: node.group,
                        blue: node.groupBlue,
                        noBorder: node.groupNoBorder,
                        footer: node.footer
                        })
                }
            });

            return groups
        }
	},
	methods: {
        ...mapMutations(["setOpenedNodeId"]),
        onMenuItemClick(event) {
            if (this.hasChildren) {
                this.setOpenedNodeId({id: this.item.id});
                this.item.opened = true;
            }
        }
	},
	created() {
        if (this.parentData) {
            this.item.parentNode = this.parentData;
        }
	},
}
</script>