import $ from 'jquery';
import PerfectScrollbar from 'perfect-scrollbar';
import 'sumoselect';


export default class Dropdown {
	constructor(el, selector, params) {
		if (!el) return false;

		this.root = el;
		this.selector = selector;

		const defaultParams = {
            captionFormat: 'Выбрано: {0}',
            captionFormatAllSelected: 'Выбрано: {0}',
			placeholder: '',
			triggerChangeCombined: true,
            nativeOnDevice: ['Android', 'BlackBerry', 'iPhone', 'iPad', 'iPod', 'Opera Mini', 'IEMobile', 'Silk']
        };

		this.params = { ...defaultParams, ...params };

		this._init();
	}

	_init() {
		this._matchHTML();
		this._initDropdown();
		this._checkChange();
	}

	_matchHTML() {
		this.select = this.root.querySelector(`[${this.selector}-select]`);
		this.placeholder = this.root.querySelector(`[${this.selector}-placeholder]`);
		this.clear = this.root.querySelector(`[${this.selector}-clear]`);
	}

	_initDropdown() {
		let ps;

		$(this.select).SumoSelect(this.params)

        $(this.select)
        .on('sumo:opened', (e) =>{
			this.root.classList.add('is-opened');
			ps = new PerfectScrollbar(this.root.querySelector('.options'),{
                wheelPropagation: false
            });
		}).on('sumo:closed', () =>{
			this.root.classList.remove('is-opened');
			if(this.params.changeCallback) this.params.changeCallback(this.select)
			ps.destroy();
		}).on('change', ()=>{
			if(this.params.changeValueCallback) this.params.changeValueCallback(this.select);
            this._checkChange();
        })
        // this.select.dispatchEvent(new Event('change'));

		if (this.clear) {
			this.clear.addEventListener("click", e=>{
				$(this.select)[0].sumo.unSelectAll();
				if(this.params.changeCallback)this.params.changeCallback(this.select)
			})
		}
		
		// Из-за глюка на iOS
		$(this.select).off('focus');
	}

	_checkChange() {
		let val = this.select.value;

		if (val) {
			this.root.classList.add('is-changed');
		} else {
			this.root.classList.remove('is-changed');
		}
	}

}

class DropdownFactory {
	constructor(props) {
		this.elements = [...document.querySelectorAll(`[${props.selector}]`)];
		this.dropdowns = [];

		this.elements.forEach(element => {
			let options = {
					rootMargin: `-${element.offsetHeight / 2}px  0px`
				};

			const io = new IntersectionObserver(entries => {
				if (entries[0].isIntersecting) {
					if (!element.classList.contains('is-inited')){
						this.dropdowns.push(
							new Dropdown(element, props.selector, props.params)
						);
						element.classList.add('is-inited')
					}
				}
			}, options);

			io.observe(element);
		});
	}
}

export { Dropdown, DropdownFactory }
