import Inputmask from "inputmask";

export default class CountControl {
	constructor(el, selector, params) {
		if (!el) return false;

		this.root = el;
		this.selector = selector;

		if (this.root.hasAttribute(`${selector}-inited`)) return false;
		this.root.setAttribute(`${selector}-inited`, true);

		const defaultParams = {

		};

		this.params = { ...defaultParams, ...params };

		this.input = this.root.querySelector(`input`);
		this.controlsDec = this.root.querySelector(`[data-control="dec"]`);
		this.controlsInc = this.root.querySelector(`[data-control="inc"]`);
		this.infoContainer = this.root.querySelector(`[data-info]`);

		this.cartValue = +this.input.value,
		this.selectedCount = this.input.value;
		this.totalValue = +this.input.value;
		this.step = this.input.getAttribute(`data-step`) || 1;
		this.max = this.input.dataset.max || 9999;
		this.min = this.input.dataset.min || this.step;
		this.realMaxValue = +this.max - (+this.max % +this.step);

		this.root.countControl = this;

		this._init();
	}

	_init() {
		this._binds();
		this._initEventListeners();
		this._initMask();

		if (this.step != 1) {
			this.infoContainerShow();
		}

		this._buttonsLimits();
		
	}

	_binds() {
		['_onFocus', '_onBlur', '_onKeydown', '_onControlButtonClick'].forEach(f => this[f] = this[f].bind(this));
	}

	_initEventListeners() {
		this.input.addEventListener('focus', this._onFocus);
		this.input.addEventListener('blur', this._onBlur);
		this.input.addEventListener('keydown', this._onKeydown);
		this.controlsDec.addEventListener('click', this._onControlButtonClick);
		this.controlsInc.addEventListener('click', this._onControlButtonClick);
	}

	_initMask() {
		const self = this;
		Inputmask({
			mask: '9{1,4}',
			showMaskOnHover: false,
			showMaskOnFocus: false,
			placeholder: ''
		}).mask(self.input);
	}

	_onFocus() {
		this.startVal = this.getValue();
	}

	_onBlur() {
		if (!this.input.value || (this.input.value == 0)) {
			this.input.value = this.min;
			this._buttonsLimits();
		}

		if (this.getValue() !== this.startVal) {
			this.setValue(this.getValue());
		}
	}

	_onKeydown(event) {
		let selected = false;
		if ((this.input.selectionStart == 0 && this.input.selectionEnd == 1) || (this.input.selectionStart == 0 && this.input.selectionEnd == this.input.value.length)) {
			selected = true;
		}
	}

	_onControlButtonClick(event) {
		event.preventDefault();

		let button = event.target,
			val = this._buttonsLimits(button);

		this.setValue(val);
	};

	_buttonsLimits(button) {
		let val = +this.input.value,
			step = +this.step;
			
			this.controlsDec.classList.remove('is-disabled')
			this.controlsInc.classList.remove('is-disabled')
		

		if (button === this.controlsInc && val + step <= this.max) {
			val = val + step;
			
		}
		else if (button === this.controlsDec && val > this.min) {
			val = val - step;
		}

		if (val >= this.max) {
			this.controlsInc.classList.add('is-disabled')
		} 

		if(val <= this.min){
			this.controlsDec.classList.add('is-disabled')
		}

		return val;
	}

	getValue() {
        return +this.input.value;
	}
	
	setValue(value) {
		let fixedValue = parseInt(this.getFixedValue(value));

		this._checkLimit(value, fixedValue, this.totalValue);

		this.prevValue = this.input.value;

		this.input.value = fixedValue;
		this.totalValue = this.input.value;
	}

	getFixedValue(value) {
		let currentValue = +value || this.min || this.getValue(),
			step = +this.step,
			max = +this.max,
			maxRemainder = max % step,
			stepRemainder = currentValue % step;

		if (stepRemainder)
			currentValue = currentValue + (step - stepRemainder);

		if (currentValue >= max)
			currentValue = max - maxRemainder;

		return currentValue;
	}

	_checkLimit(value, fixedValue, prevValue) {
		let isMaximum = false;

		if (fixedValue != prevValue) {
			if (value > this.realMaxValue) {
				isMaximum = true;
			}
		} else {
			if (value >= this.realMaxValue) {
				isMaximum = true;
			}
		}
		
		if (isMaximum) {
			this.infoContainerShow(isMaximum);
		}

		this.isMaximum = isMaximum;
	}

	infoContainerShow(isMaximum) {
		let message = '';

		if (this.step != 1) {
			message = `кратно ${this.step}`;
			this.infoContainer.innerText = message;
		}

		if (isMaximum) {
			message = `доступно ${this.max}`;
			this.infoContainer.innerText = message;

			setTimeout(() => {
				if (this.step != 1) {
					message = `кратно ${this.step}`;
					this.infoContainer.innerText = message;

				} else {
					this._infoContainerHide();
				}
			}, 1500);
		}

		this.infoContainer.classList.add('is-active');
	}

	_infoContainerHide() {
		this.infoContainer.classList.add('is-active');
		this.infoContainer.innerText = '';
	}

}

class CountControlFactory {
	constructor(props) {
		this.countControls = [];
		[...document.querySelectorAll(`[${props.selector}]`)].forEach(el => {
			this.countControls.push(
				new CountControl(el, props.selector, props.params)
			);
		});
	}
}

export { CountControl, CountControlFactory }