import Axios from 'axios';
import tingle from 'tingle.js/src/tingle';
import { Form } from '%modules%/form/form';
import YoutubeVideo from '%modules%/youtube-video/youtube-video';
import CustomTable from '%modules%/custom-table/custom-table';
import {Slider} from '%modules%/slider/slider';
import Table from '%modules%/table/table';

const getVue = () => import(/* webpackChunkName: "vue" */ 'vue');

function isSelectorValid(selector) {
    try { 
        document.createDocumentFragment().querySelector(selector); 
    } catch { 
        return false 
    }
    return true
}

class Modal {
	constructor(el, selector, params) {
		if (!el) return false;

		this.root = el;
		this.selector = selector;
		const defaultParams = {
            tingle: {
                closeButton: false,
                onOpen: () => {
                    this.root.dispatchEvent(new CustomEvent('modal:open'));
                },
                onClose: () => {
                    this.root.dispatchEvent(new CustomEvent('modal:close'));
                },
                // beforeOpen: () => {},
                // beforeClose: () => {},
            },
            preloaderTimeout: 500,
            destroyOnClose: false
        };

        let elementParams = this.root.getAttribute(this.selector);
        if (elementParams) {
            elementParams = JSON.parse(elementParams);
        }

        this.params = { ...defaultParams, ...params, ...elementParams };

        this.template = this.root.getAttribute("href") || this.params.href;
        this.ajax = (isSelectorValid(this.template)) ? false : true;
        this.url = (this.ajax) ? this.template : "";
        this.modal = null;

        this.contentLoaded = false;

		this._init();
	}

	_init() {
        this.root.addEventListener("click", this._onOpenModalClick.bind(this));
        this.root.addEventListener("modal:close", this._onCloseModal.bind(this));
    }

    _setContent() {
        this.modal = new tingle.modal(this.params.tingle);
        window.modal = this.modal;

        if (!this.ajax) {
            let modalContainer = document.querySelector(this.template);
            let template = modalContainer.innerHTML;
            
            if (this.params.vue) {
                getVue().then((data) => this._initVueModal(data.default, template));
            } else {
                this.modal.setContent(template);
                this.contentLoaded = true;
                this._initContent();
            }
        } else {
			let timeout = setTimeout(() => {
                if (this.params.preloader) this.params.preloader();
            }, this.params.preloaderTimeout);
                
            Axios({
                method: 'GET',
                url: this.url
            }).then(res => {
                clearTimeout(timeout);

                let template = res.data.template || res.data;

                if (this.params.vue) {
                    getVue().then((data) => this._initVueModal(data.default, template));
                } else {
                    this.modal.setContent(template);
                    this.contentLoaded = true;
                    this._initContent();
                }
            }).catch(error => {
                console.log(error)
            })
        }
    }

    _initVueModal(Vue, template) {
        let modal = Vue.compile(template),
        components = this.params.vueComponents || {};

        let modalCtx = this;
        let mountContainer = this.modal.getContent().appendChild(document.createElement("div"));
        
        new Vue({
            render: modal.render,
            staticRenderFns: modal.staticRenderFns,
            components: components,
            data() {
                return {
                    modalData: modalCtx.params.vueData,
                    window
                }
            },
            methods: {
                closeModal() {
                    modalCtx.modal.close()
                },
                scrollAnchors(hash) {
                    setTimeout(window.scrollAnchors(hash),0)
                }
            },
            mounted() {
                modalCtx.modal.open();
                modalCtx.contentLoaded = true;
                this.$nextTick(() => modalCtx._initContent());
            }
        }).$mount(mountContainer);
    }
    
    _initContent() {
        let content = this.modal.getContent();
        let closeButtons = [...content.querySelectorAll(`[${this.selector}-close]`)];
        closeButtons.forEach(btn => {
            btn.addEventListener("click", () => {
                this.modal.close();
            });
        });
        this._initForms()
        this._initYoutube()
        this._initCustomTable()
        this._initSliders()
        this._initInsideModals()
        this._initTable()
        if (this.params.onInit) this.params.onInit();
    }

    _initForms() {
        let modal = this.modal
		let selector = 'data-form',
			forms = [...this.modal.getContent().querySelectorAll(`[${selector}]`)];

		if (forms.length) {
            forms.forEach(form=>{
                new Form( form, selector, {callback(){modal.close()}});

            })
		}
    }

    _initYoutube(){
        let modal = this.root
		let selector = 'data-youtube',
        el = this.modal.getContent().querySelector(`[${selector}]`);
		if (el) {
			new YoutubeVideo(el, selector, modal);
		}
    }

    _initCustomTable(){
        let selector = 'data-custom-table',
        el = this.modal.getContent().querySelector(`[${selector}]`);
        if (el) {
			new CustomTable(el, selector);
		}
    }

    _initTable(){
		let selector = 'data-table',
			element = document.querySelector(`[${selector}]`);
        
        if (element) {
			new Table(element, selector);
        }

	}

    _initSliders(){
        let selector = 'data-slider',
        sliders = [...this.modal.getContent().querySelectorAll(`[${selector}]`)];

        if (sliders.length) {
            sliders.forEach(slider=>{
                new Slider(slider, selector);

            })
        
        }
    }

    _initInsideModals(){
        let selector = 'data-modal',
        btns = [...this.modal.getContent().querySelectorAll(`[${selector}]`)];

        if (btns.length) {
            btns.forEach(btn=>{
                new Modal(btn, selector);
            })
        
        }
    }

    _onOpenModalClick(e) {
        e.preventDefault();
        this.openModal();
    }

    openModal() {
        if (!this.contentLoaded) {
            this._setContent();
        }
        this.modal.open();
    }

    _onCloseModal(e) {
        if (this.params.destroyOnClose) {
            if (this.modal) this.modal.setContent('');
            this.contentLoaded = false;
        }
    }

}

class ModalFactory {
	constructor(props) {
		this.modules = [];
		[...document.querySelectorAll(`[${props.selector}]`)].forEach(el => {
			this.modules.push(
				new Modal(el, props.selector, props.params)
			);
		});
	}
}

export {Modal, ModalFactory};