import Vue from 'vue';
import $ from 'jquery';
import declOfNum from '~/js/helpers/decl-of-num';

export default {
	state: {
		filterData: window.filtersData,
		filterRequestData: []
	},
	mutations: {
		setValueChecked(state, payload) {
			console.log(payload);

			let items =  state.filterData.ITEMS

			Vue.set(items[payload.itemID].VALUES[payload.valueID], 'CHECKED', payload.checked);

		},

		setValueRadio(state, payload) {
			let items = state.filterData.ITEMS
			if(payload.checked) {
				Object.keys(items[payload.itemID].VALUES).forEach(value=>{
					Vue.set(items[payload.itemID].VALUES[value], 'CHECKED', false);
				})

				Vue.set(items[payload.itemID].VALUES[payload.valueID], 'CHECKED', payload.checked);
			} else {
				Object.keys(items[payload.itemID].VALUES).forEach(value=>{
					Vue.set(items[payload.itemID].VALUES[value], 'CHECKED', true);
				})
			}

		},

		resetFilterOption(state, selectedItems) {
			let items = state.filterData.ITEMS;

			selectedItems.forEach(selectedItem => {
				for (let itemID in items) {
					for (let valueID in items[itemID].VALUES) {

						if (selectedItem.CONTROL_NAME === items[itemID].VALUES[valueID].CONTROL_NAME) {
							if (typeof items[itemID].VALUES[valueID].CHECKED !== 'undefined') {
								Vue.set(items[itemID].VALUES[valueID], 'CHECKED', false);
							}
						}
					}
				}
			})
		},

		buildRequestData(state, payload) {
			let filterItems = state.filterData.ITEMS;

			state.filterRequestData = [];

			for (let itemID in filterItems) {
				Vue.set(filterItems[itemID], 'SELECTED_ITEMS', []);

				for (let valueID in filterItems[itemID].VALUES) {
					if (filterItems[itemID].VALUES[valueID].CHECKED) {
						state.filterRequestData.push(filterItems[itemID].VALUES[valueID]);
						filterItems[itemID].SELECTED_ITEMS.push(filterItems[itemID].VALUES[valueID]);
					}
				}

				if (filterItems[itemID].PRICE || (filterItems[itemID].DISPLAY_TYPE === 'A')) {
					let min = filterItems[itemID].VALUES.MIN,
						max = filterItems[itemID].VALUES.MAX;

					[min, max].forEach((limit, index) => {
						if (limit.HTML_VALUE && (limit.HTML_VALUE !== limit.VALUE)) {
							Vue.set(limit, 'RANGE_TYPE', `${index ? 'MAX' : 'MIN'}`);
							state.filterRequestData.push(limit);
							if (filterItems[itemID].PRICE) Vue.set(limit, 'IS_PRICE', true);
							filterItems[itemID].SELECTED_ITEMS.push(limit);
						}
					})
				}
			}
		},

		changeFilterData(state, data) {
			let filterItems = state.filterData.ITEMS,
				searchedItems = [],
				activeItems = [];

			for (let itemID in filterItems) {
				if (filterItems[itemID].SEARCH_STRING) {
					searchedItems.push(filterItems[itemID]);
				}

				if (filterItems[itemID].ACTIVE) {
					activeItems.push(filterItems[itemID]);
				}
			}

			for (let itemID in data.ITEMS) {
				for (let valueID in data.ITEMS[itemID].VALUES) {
					if (data.ITEMS[itemID].VALUES[valueID].DISABLED) {
						delete data.ITEMS[itemID].VALUES[valueID];
					}
				}
			}

			state.filterData = data;
			filterItems = state.filterData.ITEMS;

			for (let itemID in filterItems) {
				searchedItems.forEach(item => {
					if (filterItems[itemID].ID === item.ID) {
						Vue.set(filterItems[itemID], 'SEARCH_STRING', item.SEARCH_STRING);
					}
				});

				activeItems.forEach(item => {
					if (filterItems[itemID].ID === item.ID) {
						Vue.set(filterItems[itemID], 'ACTIVE', item.ACTIVE);
					}
				});

				for (let valueID in filterItems[itemID].VALUES) {
					if (filterItems[itemID].SEARCH_STRING) {
						if (filterItems[itemID].VALUES[valueID].VALUE.toLowerCase().includes(filterItems[itemID].SEARCH_STRING.toLowerCase())) {
							Vue.set(filterItems[itemID].VALUES[valueID], 'HIDDEN', false);

						} else {
							Vue.set(filterItems[itemID].VALUES[valueID], 'HIDDEN', true);
						}
					}
				}
			}


		},

		setActiveItem(state, payload) {
			let items =  state.filterData.ITEMS;

			for (let itemID in items) {
				if (items[itemID].ID === payload.itemID) {
					Vue.set(items[itemID], 'ACTIVE', payload.active);
				} else {
					Vue.set(items[itemID], 'ACTIVE', false);
				}
			}
		},
		setAllActiveItems(state, payload){
			let items =  state.filterData.ITEMS;
			for (let itemID in items) {
				if (items[itemID].ACTIVE) {
					Object.keys(items[itemID].VALUES).forEach((value)=>{
						Vue.set(items[itemID].VALUES[value], 'CHECKED', payload.checked);
					});
				}
			}
		}
	},
	getters: {
		filterData: state => {
			return state.filterData
		},
		filterItems: state => {
			Object.keys(state.filterData.ITEMS).forEach((key)=>{
				if((key === "BASE") || !state.filterData.ITEMS[key].VALUES) {
					delete state.filterData.ITEMS[key]
				}
			})
			return state.filterData.ITEMS
		},
		filterSelectedItems: state => {
			return state.filterRequestData
		},
		activeItem: state => {
			let item;

			for (let itemID in state.filterData.ITEMS) {
				if (state.filterData.ITEMS[itemID].ACTIVE) {
					item = state.filterData.ITEMS[itemID];
				}
			}

			return item;
		},
		filterCountFormated: state =>{
			return state.filterData.ELEMENT_COUNT + declOfNum(state.filterData.ELEMENT_COUNT, [' товар', ' товара', ' товаров']);
		}
	},
	actions: {
		sendFilterData({ state, commit, dispatch },payload) {
			let data = state.filterRequestData,	
				newData = {
					ajax: "y",
					filter: "y"
				}
			data.forEach(el => {
				newData[el.CONTROL_NAME] = el.HTML_VALUE;
			});

			$.ajax({
				url: window.location.href,
				data: $.param(newData),
				method: 'GET',
				dataType: 'json',
				processData: false,
				contentType: false,
				beforeSend: () => {

					payload.htmlElements.filters.classList.add('is-loading');
					payload.htmlElements.catalog.classList.add('is-loading');
				},
				success: (res) => {
					commit('changeFilterData', res);
					commit('buildRequestData');
					dispatch('updateContent', {...payload, ...{url:res.FILTER_AJAX_URL}})
				}
			});
		},
		updateContent({commit}, payload) {
            window.history.pushState(null, null, payload.url);

            $.ajax({
                url: payload.url,
                method: 'GET',
                dataType: 'json',
                processData: false,
                contentType: false,
                beforeSend: () => {
                    if(payload.htmlElements.filters)payload.htmlElements.filters.classList.add('is-loading');
					if(payload.htmlElements.catalog)payload.htmlElements.catalog.classList.add('is-loading');
                    if(payload.htmlElements.pagination) payload.htmlElements.pagination.classList.add('is-loading');

                },
                success: (res) => {
                    if(payload.more){
                        $(payload.htmlElements.catalog).append($(res.content));
                    }else{
                        payload.htmlElements.catalog.innerHTML = res.content;
                    }
                    if(payload.htmlElements.pagination) {
                        if(payload.callbackPaginationMore && payload.more) payload.callbackPaginationMore()

                        payload.htmlElements.pagination.innerHTML = res.pagination;
                        payload.htmlElements.pagination.classList.remove('is-loading');
                        if(payload.callbackPaginationUpdate) payload.callbackPaginationUpdate()
                        if(payload.callbackPaginationNoMore && !payload.more) payload.callbackPaginationNoMore()
                    }
                    if(payload.htmlElements.filters)payload.htmlElements.filters.classList.remove('is-loading');
                    if(payload.htmlElements.count)payload.htmlElements.count.innerHTML = res.count;
                    payload.htmlElements.catalog.classList.remove('is-loading');
					if(payload.callbackContentUpdate) payload.callbackContentUpdate();
					if(payload.callbackFinish) payload.callbackFinish();
                }
            });
		}
	}
}
