<script>
import SearchForm from '../search-form/search-form.vue'
import MobileMenu from '../mobile-menu/mobile-menu.vue'
import DesktopMenu from '../desktop-menu/desktop-menu.vue'
import Modal from '../modal/modal.vue'
import Tooltip from '../tooltip/tooltip.vue'
import ToggleScrollLock from '~/js/helpers/scroll-lock';
import {breakpoints} from '~/js/helpers/breakpoints';

export default {
    data() {
        return{
            fixedHeader: false,
            openedCatalog: false,
            focusedSearch: false,
            openedSearch: false, 
            openedMobileMenu: false,
            openedDesktopMenu: false,
            openedDesktopMenuSecondLevel: false,
            mobileMenuData: window.mobileMenuData,
            cart: window.cart
        }
    },
    components: {
        'search-form': SearchForm,
        'mobile-menu': MobileMenu,
        'desktop-menu': DesktopMenu,
        'modal': Modal,
        'tooltip': Tooltip
    },
    methods:{
        toggleSearch(){
            this.openedSearch = !this.openedSearch

            this.$nextTick(() => {
                let searchInput = document.querySelector('.search-form__input > input');
                if (searchInput) {
                    if (this.openedSearch) {
                       searchInput.focus();
                    }
                }
            })
           
            if(window.matchMedia(`(max-width: ${breakpoints.desktop})`).matches) {
                new ToggleScrollLock();
            }
            
        },
        toggleMobileMenu(){
            this.openedMobileMenu = !this.openedMobileMenu
            new ToggleScrollLock();
        },
        toggleDesktopMenu(){
            this.openedDesktopMenu = !this.openedDesktopMenu
            new ToggleScrollLock();
        },
        toggleDesktopMenuSecondLevel(toggle){
            if (toggle != undefined) {
                this.openedDesktopMenuSecondLevel = toggle
            } else {
                this.openedDesktopMenuSecondLevel = !this.openedDesktopMenuSecondLevel
            }
        },
    },
    mounted(){
        this.$root.$on('toggleMobileMenu', this.toggleMobileMenu)
        this.$root.$on('toggleDesktopMenuSecondLevel', this.toggleDesktopMenuSecondLevel)
    }
}
</script>