<template lang="pug">
.search-results
    .search-results__wrapper(ref="scrollContainer")
        .search-results__group(
            v-for="(group, groupIndex) in searchResponse.groups"
            :key="groupIndex"
            )
            .search-results__group-title.t-base.t-base--size--medium.t-base--color--dark-55 
                | {{group.title}}
            .search-results__items
                .search-results__item(
                    v-for="(item, itemIndex) in group.items"
                    :key="itemIndex"
                    )
                    a(:href="item.link" :target="item.blank?'_blank':false" ).search-results__item-link.link.link--color--dark-main.t-base.t-base--size--large
                        svg(
                            v-if="item.beforeTextIcon" 
                            :class="'icon-modern--'+ item.beforeTextIcon"
                            ).icon.icon--size--large.link__icon.link__icon--left
                            use(:xlink:href="templatePath+'img/sprite.svg#'+ item.beforeTextIcon +'-usage'")

                        span.link__text {{item.title}}

                    span(v-if="item.info").search-results__item-info.t-base.t-base--size--large.t-base--color--dark-55
                        | {{item.info}}
            
            a(:href="group.link").link.t-base.t-base--size--medium.t-base--weight--bold
                span.link__text {{group.linkTitle}}

    .search-results__see-all(:class="{'is-sticky':stickySeeAll}")
        a(:href="searchResponse.link").link.t-base.t-base--size--large.t-base--weight--bold
            span.link__text {{searchResponse.linkTitle}}

</template>

<script>
import PerfectScrollbar from 'perfect-scrollbar';
export default {
    name: 'search-results',
    props:{
        searchResponse: Object
    },
    data(){
        return {
            templatePath: window.SITE_TEMPLATE_PATH_JS,
            stickySeeAll: false,
            ps: null
        }
    },
    methods: {
        initPerfectScrollbar(){
            this.ps = new PerfectScrollbar(this.$refs.scrollContainer,{
                wheelPropagation: false
            })
            if(this.ps.scrollbarYActive) this.stickySeeAll = true
        },
        scrollSeeAll(){
            this.$refs.scrollContainer.addEventListener('ps-scroll-y', () => {
                if(this.ps.reach.y === 'end') {
                    this.stickySeeAll = false
                } else {
                    this.stickySeeAll = true
                }
            });
        },
        checkSeeAll(){
            if(this.ps && this.ps.scrollbarYActive && this.ps.reach.y !== 'end') {
                this.stickySeeAll = true
            } else {
                this.stickySeeAll = false
            }
        },
    },
    mounted(){
        this.initPerfectScrollbar();
        this.scrollSeeAll();
        this.checkSeeAll();
    },
    updated(){
        this.checkSeeAll();
    }
}
</script>