<script>
import PerfectScrollbar from 'perfect-scrollbar';
export default {
    props: {
        isOpened: {
            type: Boolean,
            default: false
        },
        openedSecondLevel: {
            type: Boolean,
            default: false
        },
    },
    data() {
        return {
            ps: null,
            hover: null,
            activeMenuIndex: 0,
        }
    },
    methods: {
        onMenuItemClick(event){
            if(!this.openedSecondLevel) {
                event.preventDefault()
                this.toggleSecondLevel(true)
            }
        },
        psupdate(){
            this.ps.update()
        },
        hoverItem(menuIndex, event){
            this.hover = event.target

            // setTimeout(()=>{
                if (this.hover === event.target) {
                    this.activeMenuIndex = menuIndex;
                    this.scrollToTop()
                    this.psupdate();
                }
            // },500)
        },
        scrollToTop(){
            this.$refs.ps.scrollTo({
                top: 0,
                behavior: 'smooth'
            });
        },
        toggleSecondLevel(toggle){
            this.$root.$emit('toggleDesktopMenuSecondLevel', toggle);
        }
    },
    mounted() {
        this.ps = new PerfectScrollbar(this.$refs.ps);
        this.psupdate();
    }
}
</script>