<template lang="pug">
    
</template>
<script>
export default {
	props: {
		htmlElements: Object,
		callbackPaginationUpdate: Function,
		callbackContentUpdate: Function
	},
    data() {
		return {
			chipsData: window.chipsData
		}
	},
    computed: {
        activeItem(){
            let activeItem,
            chips = this.chipsData.ITEMS;
            chips.forEach(chip => {
                if (chip.ACTIVE === true){
                    activeItem = chip
                }
            });
            return activeItem
        }
    },
    methods:{
        baseItem(chips){
            let baseItem;

            chips.forEach(chip => {
                if (chip.BASE){
                    baseItem = chip
                }
            });

            return baseItem
        },
        updateContent(url){
            this.$store.dispatch('updateContent',{
                htmlElements: this.htmlElements,
                url,
                callbackPaginationUpdate: this.callbackPaginationUpdate,
                callbackContentUpdate: this.callbackContentUpdate
            });
        },
        setActive(item, chips){
            let url = item.URL;
            chips.forEach(chip => {
                if (chip.ID === item.ID){
                    chip.ACTIVE = true
                } else {
                    chip.ACTIVE = false
                    if(chip.SUB && chip.SUB.length) {
                        chip.SUB.forEach(subChip=>{
                            subChip.ACTIVE = false
                            if (subChip.BASE){
                                subChip.ACTIVE = true
                            }
                        })
                    }
                }
            });

            this.updateContent(url)

        }
    }
}
</script>