const breakpoints = {
	'small-phone': '320px',
	'phone': '375px',
	'phone-landscape': '576px',
	'tablet': '768px',
	'tablet-landscape': '992px',
	'small-desktop': '1100px',
	'desktop': '1024px',
	'mid-desktop': '1260px',
	'large-desktop': '1440px',
	'big-desktop': '1600px',
	'extra-desktop': '2000px'	
}

function mqTest(mql, callbacks) {
	let screenTest = (e) => {
			if (e.matches) {
				if (callbacks.match) callbacks.match();
			} else {
				if (callbacks.unmatch) callbacks.unmatch();
			}
		};
	
	screenTest(mql);
	mql.addListener(screenTest);
}

export {breakpoints, mqTest};