<template lang="pug">
include ../button/button
include ../link/link

.mobile-menu(:class="{'is-opened': isOpened}")
    .mobile-menu__wrapper
        .mobile-menu__head
            .mobile-menu__back(v-if="isBackBtnShown")
                +link({
                    vue: true,
                    prefix: 'templatePath',
                    noLink: true,
                    icon:"arrow-right",
                    iconClass: "icon--rotate--180"
                })(
                    @click="onBackBtnClick").link--color--dark-55
            .mobile-menu__head-title.t-base.t-base--size--large.t-base.t-base--color--dark-55.t-base--weight--bold 
                |{{ menuTitle }}
            .mobile-menu__close
                +link({
                    vue: true,
                    prefix: 'templatePath',
                    noLink: true,
                    icon:"cross",
                    iconClass: "icon--size--large"
                })(@click.prevent="onCloseBtnClick()").link--color--dark-55
        .mobile-menu__body.mobile-menu__body--bg--blue
            .mobile-menu__container
                .mobile-menu__group(
                    v-if="groups && groups.length"
                    v-for="group in groups"
                    :class="{'mobile-menu__group--bg--blue': group.blue,'mobile-menu__group--no-border': group.noBorder,'mobile-menu__footer': group.footer}"
                    )
                    mobile-menu-item(
                        v-for="(item, index) in displayedItems"
                        v-if="item.group == group.name"
                        :key="index"
                        :item="item"
                        :data="data"
                        :opened-node="openedNode"
                    )
                mobile-menu-item(
                    v-for="(item, index) in displayedItems"
                    v-if="!item.group"
                    :key="index"
                    :item="item"
                    :data="data"
                    :opened-node="openedNode"
                )
</template>

<script>
import store from './store'
import { mapGetters, mapMutations } from 'vuex';
import MobileMenuItem from './components/mobile-menu-item.vue'

export default {
    store,
    name: "mobile-menu",
    props: {
        isOpened: {
            type: Boolean
        },
        data: {
            type: Array, 
            default: ()=>[]
        },
        defaultTitle: {
            type: String,
            default: ()=>'Меню'
        }
    },
    data() {
        return {
            defaultTitle: "Меню",
            templatePath: window.SITE_TEMPLATE_PATH_JS
        }
    },
    watch: {
        data() {
            this.initData(this.data);
        },
    },
    components: {
        "mobile-menu-item": MobileMenuItem
    },
    computed: {
        ...mapGetters(["openedNodeId"]),
        openedNode() {
            return this.data.find((node => node.id === this.openedNodeId));
        },
        isBackBtnShown() {
            return !!this.openedNode;
        },
        menuTitle() {
            return (this.openedNode) ? this.openedNode.text : this.defaultTitle;
        },
        displayedItems() {
            return this.data.filter(node => node.top);
        },
        groups(){
            let groups = []
            this.displayedItems.forEach(node => {
                if (node.group && !groups.filter(e=> e.name === node.group).length) {
                    groups.push({
                        name: node.group,
                        blue: node.groupBlue,
                        noBorder: node.groupNoBorder,
                        footer: node.footer
                        })
                }
            });
            return groups
        }
    },
    methods: {
        ...mapMutations(["setOpenedNodeId"]),
        initData(items) {
            if (items && items.length > 0) {
                for (let i in items) {
                    this.$set(items[i], "opened", false);
                    this.$set(items[i], "parentNode", null);
                }
            }
        },
        onBackBtnClick() {
            this.openedNode.opened = false;
            if (!this.openedNode.parentNode) {
                this.setOpenedNodeId({id: null});
            } else {
                this.setOpenedNodeId({id: this.openedNode.parentNode.id});
            }
        },
        onCloseBtnClick() {
            this.setOpenedNodeId({id: null});
            this.$root.$emit('toggleMobileMenu');
        }
    },
    created() {
        this.initData(this.data);
    }
}
</script>