import Swiper from 'swiper';
import SwiperCore, { Navigation, Pagination, EffectFade  } from 'swiper/core';
import Odometer from 'odometer';
// Install modules
SwiperCore.use([Navigation, Pagination, EffectFade ]);

class Slider {
    constructor(el, selector, params){
		this.root = el;
        this.selector = selector;
        if (!el) return false;
        if (this.root.hasAttribute(`${selector}-inited`)) return false;
		this.root.setAttribute(`${selector}-inited`, true);
        this.params = params;
        this.defaultParams = {};
        this.dataParams = this.root.getAttribute(`${this.selector}`) ? JSON.parse(this.root.getAttribute(`${this.selector}`)) : {};
        this._init()
    }
    _init(){
        this._matchHTML()
        this._setDefaultParams()
        this._initSwiper()
    }

    _matchHTML() {
        this.pagination = this.root.querySelector(`[${this.selector}-pagination]`);
        this.count = this.root.querySelector(`[${this.selector}-count]`);
        this.arrowRight = this.root.querySelector(`[${this.selector}-navigation-right]`);
        this.arrowLeft = this.root.querySelector(`[${this.selector}-navigation-left]`);
	}

    _setDefaultParams(){
        if(this.pagination) {
            this.defaultParams.pagination = {
                el: this.pagination,
                type: "bullets",
                bulletClass: "slider-dots__item",
                bulletActiveClass: "slider-dots__item--active",
                clickable: true
            }
        }
        if(this.arrowRight || this.arrowLeft) {
            this.defaultParams.navigation = {}

            if(this.arrowRight) this.defaultParams.navigation.nextEl = this.arrowRight
            if(this.arrowLeft) this.defaultParams.navigation.prevEl = this.arrowLeft
        }
        if(this.count) {
            const od = new Odometer({
                el: this.count,
                value: 1
            });
            this.defaultParams.on ={
                slideChange: (e)=>{
                    this.count.innerHTML = '0' + (e.activeIndex + 1)
                }
            }
        }
    }

    _initSwiper(){
        let inited = false;
       
        const io = new IntersectionObserver(entries => {
            if (entries[0].isIntersecting) {
                if (!inited) {
                    this.swiper = new Swiper(this.root, {...this.defaultParams, ...this.dataParams, ...this.params});
                    inited = true;
                }
            }
        });

        io.observe(this.root);
    }
}
class SliderFactory {
	constructor(props) {
		this.sliders = [];

		[...document.querySelectorAll(`[${props.selector}]`)].forEach(el => {
			this.sliders.push(
				new Slider(el, props.selector, props.params)
			);
		});
	}
}

export {Slider, SliderFactory}