export default {
	state: {
		openedNodeId: null,
	},
	mutations: {
		setOpenedNodeId: function(state, payload) {
			state.openedNodeId = payload.id;
		},
	},
	getters: {
		openedNode: function(state) {
			return state.openedNode;
		},
		openedNodeId: function(state) {
			return state.openedNodeId;
		},
	},
	actions: {
	}
}