import tippy, {followCursor} from 'tippy.js';

class Tooltip {
	constructor(el, selector, params) {
		if (!el) return false;

		this.tooltipBtn = el;
		this.selector = selector;


		const defaultParams = {
			appendTo: document.body,
			theme: 'custom',
			content: 'Tooltip',
			placement: 'top',
			interactiveBorder: 5,
			interactive: true,
			allowHTML: true,
			plugins: [followCursor],
			content: (btn) => {
				const id = btn.getAttribute(this.selector);
				const template = id 
					? document.querySelector(`[${this.selector}-content="${id}"]`) 
					: btn.querySelector(`[${this.selector}-content]`);
				return template.innerHTML;
			},
			onShow: (instance) => {
				this.tooltipBtn.classList.add('is-selected')
			},
			onHide: (instance) => {
				this.tooltipBtn.classList.remove('is-selected')
			},
		};

		let dataParams = this.tooltipBtn.getAttribute(`${this.selector}-params`) ? JSON.parse(this.tooltipBtn.getAttribute(`${this.selector}-params`)) : {};

		this.params = {...defaultParams, ...dataParams, ...params};

		this._init();
	}

	_init() {
		// TODO IE11 bug
		this.tippy = tippy(this.tooltipBtn, this.params);
	}
	
}

class TooltipFactory {
	constructor(props) {
		this.tooltips = [];

		[...document.querySelectorAll(`[${props.selector}]:not(.is-inited)`)].forEach(el => {
			this.tooltips.push(
				new Tooltip(el, props.selector, props.params)
			)
			el.classList.add('is-inited');
		});
	}
}

export {Tooltip, TooltipFactory}
